@import "../../global.scss";

.intro {
    background-color: black;
    display: flex;
    color: white;

    @include mobile {
        flex-direction: column;
        align-items: center;
    }

    .left {
        flex: 0.5;
        overflow: hidden;
        margin-top: 5%;

        .imageContainer {
            width: 800px;
            height: 800px;
            background-color: #dd6691;
            border-radius: 50%;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile {
                width: 250px;
                height: 250px;
            }

            img {
                height: 90%;
            }
        }
    }
    .right {
        flex: 0.5;
        position: relative;

        .wrapper {
            width: 100%;
            height: 100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include mobile {
                padding-left: 0;
                align-items: center;
            }

            h1 {
                font-size: 60px;
                margin: 10px 0;
                @include mobile {
                    font-size: 50px;
                }
            }
            h2 {
                font-size: 35px;
                @include mobile {
                    font-size: 25px;
                }
            }
            h3 {
                font-size: 30px;
                @include mobile {
                    font-size: 20px;
                }

                span {
                    font-size: inherit;
                    color: #dd6691;
                }

                .ityped-cursor {
                    animation: blink 1s infinite;
                }

                @keyframes blink {
                    50% {
                        opacity: 1;
                    }
                    100% {
                        opacity: 0;
                    }
                }
            }
        }
        a {
            position: absolute;
            bottom: 40px;
            left: 40%;

            @include mobile {
                bottom: 1px;
            }

            img {
                width: 50px;
                animation: arrowBlink 2s infinite;

                @include mobile {
                    width: 30px;
                }
            }

            @keyframes arrowBlink {
                100% {
                    opacity: 0;
                }
            }
        }
    }
}
